*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal), url("NeueMontreal-Medium.ef4b1f67.otf") format("opentype");
  font-weight: 500;
  font-display: swap;
}

@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal), url("NeueMontreal-Regular.2fa333b1.otf") format("opentype");
  font-weight: 400;
  font-display: swap;
}

body {
  color: var(--black);
  background-color: #e8e8e2;
  font-family: Neue Montreal;
  font-weight: 400;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load {
  margin: 0 auto;
  display: flex !important;
}

.load-discount {
  padding-top: 25px;
  flex-direction: column !important;
  display: flex !important;
}

.load-total {
  margin-bottom: 8px;
  margin-right: 25px;
}

.loader {
  color: var(--green);
  width: 4px;
  aspect-ratio: 1;
  border-radius: 50%;
  margin: 0 auto;
  animation: l21 .5s linear infinite alternate;
  transform: translateX(-38px);
  box-shadow: 19px 0 0 7px, 38px 0 0 3px, 57px 0;
}

@media only screen and (min-width: 1272px) {
  .products__list.load {
    height: 1169px;
    width: 925px !important;
  }

  .popular__list.load {
    height: 546px;
    width: 275px;
  }

  .discount__list.load-discount {
    width: 283px;
    height: 484px;
  }
}

@media only screen and (min-width: 768px) {
  .products__list.load {
    height: 1536px;
    width: 704px !important;
  }

  .popular__list.load {
    height: 322px;
    width: 704px;
  }

  .popular__list.load .loader {
    width: 5px;
    height: 5px;
    display: flex;
  }

  .discount__list.load-discount {
    width: 704px;
    height: 322px;
  }
}

@media only screen and (min-width: 320px) {
  .products__list.load {
    height: 1169px;
    width: 343px;
  }

  .popular__list.load {
    height: 546px;
    width: 275px;
  }

  .discount__list.load-discount {
    width: 343px;
    height: 484px;
  }
}

.products__list .loader {
  width: 5px;
  height: 5px;
  display: flex;
}

@keyframes l21 {
  50% {
    box-shadow: 19px 0 0 3px, 38px 0 0 7px, 57px 0 0 3px;
  }

  100% {
    box-shadow: 19px 0, 38px 0 0 3px, 57px 0 0 7px;
  }
}

